import axios from '@/libs/api.request'
import store from '@/store'
import {getVoteList} from "./vote";

const baseUrl = 'department_work_plans/';

export const getWorkPlanList = ({current_page = 0, filter = {}, with: $with}) => {
    let data = {
        current_page,
        filter,
        with: ['parent'].concat($with ? $with : []) //with是js保留词 所以加了$
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        method: 'get',
        params: data,
    })
}

export const getUserWorkPlanList = ({current_page = 0, filter = {}, with: $with}) => {
    let data = {
        current_page,
        filter: Object.assign(filter, {initiate_user_id: store.state.common.user_id}),
        with: $with //with是js保留词 所以加了$
    }
    return getWorkPlanList(data);
}

export const getWorkPlan = (id) => {
    let data = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'get',
        params: data,
    })
}

export const createWorkPlan = (d) => {
    const data = {
        data: d,
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl,
        method: 'post',
        data,
        params,
    })
}

export const updateWorkPlan = (id, d) => {
    const data = {
        data: d,
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'put',
        data,
        params,
    })
}

export const deleteWorkPlan = (id) => {
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'delete',
        params,
    })
}

