<template>
    <div>
        <TopButtons/>
        <div style="height: 10px;"></div>
        <a-card>
            <Check :article="article"/>
        </a-card>
    </div>
</template>

<script>
    import TopButtons from '../工作规划列表/components/TopButtons'
    import Check from './components/Check'
    import {getActivityPlan} from '@/api/departmentActivityPlan'

    export default {
        components: {
            TopButtons, Check
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        data() {
            return {
                article: {},
            }
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            }
        },
        methods: {
            init() {
                getActivityPlan(this.thisId)
                    .then(res => {
                        this.article = res.data;
                        console.log( this.article );
                    })
            }
        },
        mounted() {
            this.init();
        }
    }
</script>
